.sign-in-container{
	width : 99vw;
	height : 95vh;
	display: flex;
	justify-content: center;
	align-items: center;

	.google-icon{
	width : 10vw;
	height : 20vh;
	border-radius : 25px;
	transition: all 0.2s;
	cursor: pointer;

	&:hover{
		transform: translateY(-10px);
		box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
	}

	&:active{
		transform: translate(2px);
		box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
	}
}
}
