.Header-bar{
	width: 95%;
	height: 12vh;
	padding: 0px 10px;
	position: absolute;
	left : 2vw;
	right : 5vw;
	background-color: white;
	border-radius: 5px;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	display: flex;
	justify-content: space-between;
	align-items: center;

	.header-logo{
		width : 5em;

		&:hover{
			opacity: 0.5;
			cursor: pointer;
		}
		animation : imgAnime 1s;
	}

	@keyframes imgAnime{
		40%{
			transform: scale3d(2.0, 2.0, 2.0);
		}

		70%{
			transform : scale3d(0.5, 0.5, 0.5);
		}
		100%{
			transform: scale3d(1.0, 1.0, 1.0);
		}
	}




	.Header-font{
		font-size: 20px;

		&.header-option{
			width: 12em;
			display: flex;
			justify-content: space-between;

		}

		.header-signout{
			&:hover{
				cursor: pointer;
				text-decoration: underline;
				opacity: 0.5;
			}
		}
	}
}